import * as $ from 'jquery';
import { sendMessage, showOverlay } from './dialogs';
import { fabric } from './office-ui-fabric';
import { getUrlParams, htmlEncode } from './utilities';

const messageBarClasses = {
  'error': 'ms-MessageBar--error',
  'severe-warning': 'ms-MessageBar--severeWarning',
  'success': 'ms-MessageBar--success',
  'warning': 'ms-MessageBar--warning'
};

Office.initialize = () => {
  const $dialog = $('body');
  // tslint:disable:no-unused-expression
  $('.ms-Overlay').each((_, overlay) => void new fabric.Overlay(overlay));
  $('.ms-TextField').each((_, textField) => void new fabric.TextField(textField));
  $('.ms-Button').each((_, button) => void new fabric.Button(button));
  // tslint:enable:no-unused-expression

  const parameters = getUrlParams(window.location.search);
  $dialog.addClass(messageBarClasses[parameters.type] || '');

  if (parameters.title) {
    $('#app-Title').text(parameters.title);
  }
  else {
    $('#app-Title').hide();
  }

  $('#app-Message').html(parameters.message.split('').map(text => htmlEncode(text)).join('<br>'));
  $('#app-DefaultButton').on('click', () => {
    showOverlay($dialog);
    setTimeout(() => sendMessage('dialogResult', true), 0);
  });
};
